"use client";

import { useEffect, useState } from 'react';
import Link from 'next/link';
import Card from '../../components/Card';
import Button from '../../components/Button';
import Input from '../../components/Input';
import Spinner from '../../components/Spinner';

interface Doctor {
  _id: string;
  full_name: string;
  specializations: string[];
  clinic_affiliations?: any[];
}

export default function DoctorsPage() {
  const [doctors, setDoctors] = useState<Doctor[]>([]);
  const [filteredDoctors, setFilteredDoctors] = useState<Doctor[]>([]);
  const [specialization, setSpecialization] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    fetchDoctors();
  }, []);

  const fetchDoctors = async () => {
    try {
      setLoading(true);
      const response = await fetch('/api/doctors');
      if (!response.ok) throw new Error('Failed to load doctors');
      const data = await response.json();
      setDoctors(data);
      setFilteredDoctors(data);
    } catch (err: any) {
      setError(err.message || 'Error fetching doctors');
    } finally {
      setLoading(false);
    }
  };

  const handleFilter = async (spec: string) => {
    setSpecialization(spec);

    if (!spec) {
      setFilteredDoctors(doctors);
      return;
    }

    try {
      const response = await fetch(`/api/doctors/search?specialization=${encodeURIComponent(spec)}`);
      if (!response.ok) throw new Error('Search failed');
      const data = await response.json();
      setFilteredDoctors(data);
    } catch (err) {
      console.error('Error searching doctors:', err);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-slate-50 via-white to-slate-50">
          <div className="site-container">
        {/* Header */}
        <div className="mb-16 space-y-4">
          <div className="inline-flex items-center px-4 py-2 rounded-full bg-indigo-100 text-indigo-700 text-sm font-semibold gap-2">
            <span>üè•</span> Expert Doctors
          </div>
          <h1 className="text-5xl lg:text-6xl font-bold text-slate-900">Find Your Doctor</h1>
          <p className="text-xl text-slate-600 max-w-2xl leading-relaxed">Search verified healthcare professionals by specialization and find the perfect match.</p>
        </div>

        {/* Search Section */}
          <div className="mb-12 bg-white rounded-2xl p-8 shadow-sm border border-slate-100">
          <label className="block text-sm font-semibold text-slate-700 mb-4 uppercase tracking-wide">Search by Specialization</label>
          <div className="flex flex-col md:flex-row gap-4">
            <Input
              type="text"
              placeholder="e.g., Cardiology, Dermatology, Dentistry..."
              value={specialization}
              onChange={(e) => handleFilter((e.target as HTMLInputElement).value)}
              className="flex-1 px-6 py-3 rounded-lg text-base font-medium placeholder-slate-500"
            />
            <div className="flex items-center px-6 py-3 bg-indigo-50 rounded-lg border border-indigo-200">
              <span className="text-slate-700 font-bold text-lg">{filteredDoctors.length}</span>
              <span className="text-slate-600 font-medium ml-2 hidden sm:inline">Found</span>
            </div>
          </div>
        </div>

        {/* Content */}
        {loading ? (
          <div className="flex flex-col items-center justify-center py-24 space-y-4">
            <Spinner className="h-12 w-12 text-indigo-600" />
            <p className="text-slate-600 font-medium">Finding doctors...</p>
          </div>
        ) : error ? (
          <div className="bg-gradient-to-r from-red-50 to-orange-50 border border-red-200 rounded-2xl p-8 text-center">
            <p className="text-red-700 font-semibold text-lg">‚ö†Ô∏è {error}</p>
            <Button onClick={() => window.location.reload()} className="mt-4">Retry</Button>
          </div>
        ) : filteredDoctors.length === 0 ? (
          <div className="bg-white rounded-2xl border-2 border-dashed border-slate-300 p-16 text-center space-y-4">
            <p className="text-5xl">üë®‚Äç‚öïÔ∏è</p>
            <p className="text-slate-600 text-lg font-medium">No doctors found</p>
            <p className="text-slate-500">Try a different specialization or browse all doctors.</p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {filteredDoctors.map((doctor) => (
              <Link key={doctor._id} href={`/doctors/${doctor._id}`} className="group h-full">
                <Card className="h-full hover:shadow-2xl transition-all duration-300 hover:-translate-y-1 p-8 space-y-6 border border-slate-100 hover:border-indigo-300 bg-white flex flex-col">
                  {/* Top Section with Avatar */}
                  <div className="flex items-center gap-4">
                    <div className="w-16 h-16 rounded-full bg-gradient-to-br from-indigo-100 to-blue-100 flex items-center justify-center text-3xl group-hover:scale-110 transition-transform shrink-0 border-2 border-indigo-200">
                      üë®‚Äç‚öïÔ∏è
                    </div>
                    <div className="flex-1 space-y-1">
                      <h2 className="text-lg font-bold text-slate-900 group-hover:text-indigo-600 transition-colors">
                        Dr. {doctor.full_name}
                      </h2>
                      <p className="text-sm text-slate-500 font-medium">Medical Professional</p>
                    </div>
                  </div>

                  {/* Info Section */}
                  <div className="space-y-4 flex-1 py-4 border-y border-slate-100">
                    <div>
                      <p className="text-xs font-bold text-slate-600 uppercase tracking-wide mb-3">Specializations</p>
                      <div className="flex flex-wrap gap-2">
                        {(doctor.specializations || ['General Practice']).slice(0, 3).map((spec, idx) => (
                          <span key={idx} className="inline-flex items-center rounded-full bg-indigo-50 text-indigo-700 px-3 py-1.5 text-xs font-semibold border border-indigo-200">
                            {spec}
                          </span>
                        ))}
                        {doctor.specializations && doctor.specializations.length > 3 && (
                          <span className="inline-flex items-center rounded-full bg-slate-100 text-slate-700 px-3 py-1.5 text-xs font-semibold border border-slate-200">
                            +{doctor.specializations.length - 3}
                          </span>
                        )}
                      </div>
                    </div>

                    {doctor.clinic_affiliations && doctor.clinic_affiliations.length > 0 && (
                      <div className="text-sm text-slate-600 font-medium flex items-center gap-2">
                        <span>üè•</span>
                        <span>{doctor.clinic_affiliations.length} clinic{doctor.clinic_affiliations.length !== 1 ? 's' : ''}</span>
                      </div>
                    )}
                  </div>

                  {/* Bottom Section */}
                  <div className="flex items-center justify-between pt-2">
                    <span className="text-sm font-semibold text-slate-700">View Profile</span>
                    <span className="text-indigo-600 font-bold group-hover:translate-x-1 transition-transform">‚Üí</span>
                  </div>
                </Card>
              </Link>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
